<template>
  <div>
    <div class="stat__header">
      <div class="fl">页面数据</div>
    </div>
    <el-row class="stat__body" v-loading="loading">
      <el-col :span="24" class="stat__inner">      
        <div id="activeChart" class="line-chart"></div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import chartMixins from '../mixins.js'
import { fluxPageData } from '@/api/statistics/index.js'
export default {
  mixins: [chartMixins],
  data() {
    return {
      el: 'activeChart',
      optionsData: {
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'shadow'
          }
        },
        grid: {
          left: 30,
          top: 0,
          right: 30,
          bottom: 0,
          containLabel: true
        },
        xAxis: {
          type: 'value'
        },
        yAxis: {
          splitLine: {
            show: false
          },
          type: 'category',
          data: []
        },
        series: [
          {
            name: '访问量',
            type: 'bar',
            barMaxWidth: 30,
            data: []
          }
        ]
      }

    }
  },
  methods: {
    getData() {
      this.loading = true
      fluxPageData(this.queryParams)
        .then(res => {
          if (res.code === 200) {
            this.optionsData.yAxis.data = res.data.yData
            this.optionsData.series[0].data = res.data.xData
            this.chart.setOption(this.optionsData)
          }
        })
        .finally(() => {
          this.loading = false
        })
    }
  }
}
</script>
<style lang="scss" scoped>
.line-chart {
  width: 100%;
  height: 280px;
}
</style>



