<template>
<div class="stat__layout">
 <timer @change="getTime"></timer>
  <general :time-data="value" class="mt-20"></general>
 <el-row :gutter="20" >
 <el-col :xs="24" :sm="24" :md="12" :lg="14" :xl="14" class="mt-20">
    <pv-page  :time-data="value"></pv-page>
 </el-col>
  <el-col :xs="24" :sm="24" :md="12" :lg="10" :xl="10" class="mt-20">
   <pv-source  :time-data="value"></pv-source>
 </el-col >
 </el-row>
 <deep :time-data="value" class="mt-20"></deep>
 <pv-area :time-data="value" class="mt-20"></pv-area>
</div>
</template>
<script>
import timer from './../components/time'
import pvSource from './source'
import pvPage from './page'
import general from './general'
import deep from './deep'
import pvArea from './area'
export default {
  components: {
    pvPage,
    pvSource,
    general,
    pvArea,
    deep,
    timer
  },
  data() {
    return {
      value: ['', '']
    }
  },
  methods: {
    getTime(time) {
      this.value = time
    }
  }

}
</script>

<style>
</style>



