<template>
  <div>
    <div class="stat__header">
      <div class="fl">访客地域分布</div>
    </div>
    <el-row class="stat__body" v-loading="loading">
      <el-col :xs="24" :sm="24" :md="24" :lg="10" :xl="10" class="stat__inner">
        <div id="areaChart" class="line-chart"></div>
      </el-col>
      <el-col :xs="24" :sm="24" :md="24" :lg="14" :xl="14" class="border-left">
        <div class="table-box">
          <table class="stat-inner__table mt-20">
            <tr>
              <th style="width:20%">序号</th>
                <th style="width:20%">地区</th>
              <th style="width:20%">访问量</th>
              <th style="width:20%">访客数</th>
              <th style="width:20%">占比</th>
            </tr>
          </table>
          <div class="table-box__inner">
            <table class="stat-inner__table">
              <tr v-for="(item,index) in tableData" :key="index">
                <td style="width:20%">{{index+1}}</td>
                   <td style="width:20%">{{item.provinceName}}</td>
                <td style="width:20%">{{item.userPv}}</td>
                <td style="width:20%">{{item.userUv}}</td>
                <td style="width:20%">{{item.scale}}</td>
              </tr>
            </table>
          </div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>
<script>
import chartMixins from '../mixins.js'
import echarts from 'echarts'
const geoJson = require('./../../../../../static/lib/china.json')
import { statisticsAreaGet } from '@/api/statistics/index.js'

export default {
  mixins: [chartMixins],
  data() {
    return {
      el: 'areaChart',
      optionsData: {
        tooltip: {
          trigger: 'item',
          formatter: function(params, ticket, callback) {
            var res = params.name + '<br/>'
            var data = params.data
            if (params.data) {
              res += '访客数：' + data.value[0] + '</br>' + '访问量：' + data.value[1]
            } else {
              res += '访客数：0</br>访问量：0'
            }
            return res
          }
        },
        visualMap: {
          realtime: false,
          calculable: true,
          left: 20,
          inRange: {
            color: ['lightskyblue', 'yellow', 'orangered']
          }
        },
        series: [
          {
            name: '访问区域分布',
            type: 'map',
            mapType: 'china', // 自定义扩展图表类型
            itemStyle: {
              normal: { label: { show: true }},
              emphasis: { label: { show: true }}
            },
            data: []
          }
        ]
      },
      tableData: []
    }
  },
  methods: {
    getData() {
      this.loading = true
      statisticsAreaGet(this.queryParams)
        .then(res => {
          if (res.code === 200) {
            const areaData = []
            res.data.areaList.map(v => {
              const obj = {
                name: v.provinceName,
                value: [v.userUv, v.userPv]
              }
              areaData.push(obj)
            })
            this.optionsData.series[0].data = areaData
            this.tableData = res.data.areaList
            this.chart.setOption(this.optionsData)
          }
        })
        .finally(() => {
          this.loading = false
        })
    },
    initChart() {
      // 初始化图表
      this.$nextTick(() => {
        if (this.el && this.el !== '') {
          echarts.registerMap('china', geoJson)
          this.chart = echarts.init(document.getElementById(this.el), 'walden')
        }
      })
    }
  }
}
</script>
<style lang="scss" scoped>
.line-chart {
  width: 100%;
  height: 450px;
}
.stat-inner__table{
  text-align: center;
}
.table-box {
  height: 470px;
  width: 100%;
  padding: 0 20px;
  &__inner {
    height: 400px;
    overflow: auto;
    width: 100%;
  }
}
</style>



