<template>
  <div>
    <div class="stat__header">
      <div class="fl">访问来源</div>
    </div>
    <el-row class="stat__body" v-loading="loading">
      <el-col :span="24">
        <div class="table-box">
          <table class="stat-inner__table mt-20">
            <tr>
              <th style="width:10%">序号</th>
              <th style="width:30%">类型</th>
              <th style="width:30%">访问量</th>
              <th style="width:30%">访客数</th>
            </tr>
          </table>
          <div class="table-box__inner">
            <table class="stat-inner__table">
              <tr v-for="(item,index) in tableData" :key="index">
                <td style="width:10%">{{index+1}}</td>
                <td style="width:30%">{{item.name}}</td>
                <td style="width:30%">{{item.sourceData}}</td>
                <td style="width:30%">{{item.sourceUser}}</td>
              </tr>
            </table>
          </div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import chartMixins from '../mixins.js'

import { fluxVisitSource } from '@/api/statistics/index.js'
export default {
  mixins: [chartMixins],
  data() {
    return {
      tableData: []
    }
  },
  methods: {
    getData() {
      this.loading = true
      fluxVisitSource(this.queryParams)
        .then(res => {
          if (res.code === 200) {
            this.tableData = res.data
          }
        })
        .finally(() => {
          this.loading = false
        })
    }
  }
}
</script>
<style lang="scss" scoped>

.table-box {
  height: 300px;
  width: 100%;
  padding: 0 20px;
  &__inner {
    height: 250px;
    overflow: auto;
    width: 100%;
  }
}
</style>



