<template>
  <div>
    <div class="stat__header">
      <div class="fl">流量概况
        <el-tooltip placement="right" effect="light">
          <div slot="content">
          访客数（UV）： 访问站点的用户数，单个用户访问多次记一次<br>
          页面流量（PV）：访问站点页面次数，同一用户访问多次计算多次<br>
          人均流量：页面流量 / 访客数<br>
          付款订单数：成功下单且付款的订单数，统一下单按拆单后数量计算<br>
          跳出率：指用户访问一次页面后退出行为的访客数占所有访客数的比率<br>
          商品访问转化率：访问商品详情页面次数占页面流量的比率<br>
          访客加购率：加入购物车行为的访客数（相同户多次行为只计算一次）占所有访客数的比率<br>
          访客支付转化率：下单且支付行为的访客数（相同用户多次行为只计算一次） 占所有访客数的比率<br>
          </div>
          <i class="el-icon-question"></i>
        </el-tooltip>
      </div>
    </div>
    <el-row class="stat__body" v-loading="loading">
      <swiper :options="swiperOption" ref="mySwiper" class="pv-swiper">
        <swiper-slide
          class="pv-slide"
          v-for="(item,index) in swiperItems"
          :key="index"
          :style="activeSwiperItem.indexOf(item)>-1
                  ?{borderColor:color}:{}"
                  @click.native="changeActiveItem(item)"
        >
          <p>{{item}}</p>
          <span class="count-val">{{swiperData[index]}}</span>
        </swiper-slide>
      </swiper>
      <div>
        <div class="swiper-button-prev" slot="button-prev"></div>
        <div class="swiper-button-next" slot="button-next"></div>
      </div>
      <el-col :span="24" class="stat__inner">
        <div id="generalChart" class="line-chart"></div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import chartMixins from '../mixins.js'
import { getfluxData } from '@/api/statistics/index.js'
import 'swiper/dist/css/swiper.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
export default {
  mixins: [chartMixins],
  components: {
    swiper,
    swiperSlide
  },
  data() {
    return {
      el: 'generalChart',
      optionsData: {
        tooltip: {
          trigger: 'axis',
          formatter: function(datas) {
            let str = `${datas[0].axisValue}<br/>`
            datas.forEach(v => {
              let tmp = ''
              if (v.seriesName.indexOf('率') > -1) {
                tmp = `${v.marker} ${v.seriesName}:${v.data}%<br/>`
              } else {
                tmp = `${v.marker} ${v.seriesName}:${v.data}<br/>`
              }
              str += tmp
            })
            return str
          }
        },
        legend: {
          data: [],
          left: 0,
          top: 0
        },
        grid: {
          top: 40,
          left: 0,
          right: 0,
          bottom: '3%',
          containLabel: true
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          splitLine: {
            show: false
          },
          data: ['10-01', '10-02', '10-03', '10-04', '10-05', '10-06', '10-07']
        },
        yAxis: [
          {
            axisLine: {
              show: false
            },
            splitLine: {
              show: false
            },
            type: 'value'
          },
          {
            axisLine: {
              show: false
            },
            splitLine: {
              lineStyle: {
                show: false
              }
            },
            type: 'value',
            axisLabel: {
              formatter: value => {
                var texts = []
                const obj = value
                texts.push(obj + '%')
                return texts
              }
            }
          }
        ],
        series: []
      },
      swiperOption: {
        slidesPerView: 4,
        spaceBetween: 20,
        slidesPerGroup: 4,
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev'
        }
      },
      swiperItems: [],
      swiperData: [],
      chartData: [],
      activeSwiperItem: [
        '访客数(UV)',
        '页面流量(PV)',
        '跳出率',
        '商品访问转换率'
      ]
    }
  },
  computed: {
    swiper() {
      return this.$refs.mySwiper.swiper
    },
    color() {
      return this.$store.state.system.color
    }
  },
  methods: {
    getData() {
      this.loading = true
      getfluxData(this.queryParams)
        .then(res => {
          if (res.code === 200) {
            this.chartData = res.data.chartData
            this.optionsData.legend.data = this.activeSwiperItem
            this.optionsData.series = this.changeLine(this.activeSwiperItem)
            this.optionsData.xAxis.data = res.data.showTime
            this.swiperItems = res.data.showName
            this.swiperData = res.data.showData
            this.chart.setOption(this.optionsData)
          }
        })
        .finally(() => {
          this.loading = false
        })
    },
    changeActiveItem(item) {
      const index = this.activeSwiperItem.indexOf(item)
      if (index > -1) {
        this.activeSwiperItem.splice(index, 1)
      } else {
        this.activeSwiperItem.push(item)
      }
      this.optionsData.series = Object.assign([], this.changeLine(this.activeSwiperItem))
      this.chart.setOption(this.optionsData, true)
    },
    changeLine(datas) {
      const series = []
      this.chartData.forEach(v => {
        // 这里修改配置项
        const obj = {
          name: v.name,
          data: v.data,
          type: 'line',
          smooth: true,
          yAxisIndex: v.name.indexOf('率') > -1 ? 1 : 0
        }
        if (datas.indexOf(v.name) > -1) {
          series.push(obj)
        }
      })
      return series
    }
  }
}
</script>
<style lang="scss" scoped>
.line-chart {
  width: 100%;
  height: 280px;
}
.pv-slide {
  min-height: 70px;
  border: 1px solid #f0f0f0f0;
  box-sizing: border-box;
  padding: 15px;
  font-size: 14px;
  color: #666;
  cursor: pointer;
}
.pv-swiper {
  width: 85%;
  margin: 15px auto;
}
.swiper-button-prev,
.swiper-button-next {
  top: 55px;
}
.swiper-button-prev,
.swiper-container-rtl .swiper-button-next {
  left: 5%;
}
.swiper-button-next,
.swiper-container-rtl .swiper-button-prev {
  right: 5%;
}
</style>



